import photo1 from '../assets/images/community/photo01.png';
import photo2 from '../assets/images/community/photo02.png';
import photo3 from '../assets/images/community/photo03.png';
import photo4 from '../assets/images/community/photo04.png';

const TutorialSection = () => (
  <section id="c05" className="section">
    <div className="inner">
      <div className="Photo">
        {[{
          img: photo1,
          title: '[소니코리아] A7M3 실내 촬영 메뉴얼',
          desc: '사린이들 모여라\n조작법이 어려우시다면 Click!'
        }, {
          img: photo2,
          title: '[소니코리아] A7R5 실내 영상 촬영 튜토리얼',
          desc: '고화질 영상으로 남기고 싶다면\nClick!'
        }, {
          img: photo3,
          title: '[소니코리아] 야간 촬영 튜토리얼',
          desc: '야간에도 좋은 사진을 건지고 싶다면\nClick!'
        }, {
          img: photo4,
          title: '[소니코리아] Lightroom 편집 튜토리얼',
          desc: '사진 이쁘게 찍고 나만의 개성을\n창조하고 싶다면 Click!'
        }].map((item, idx) => (
          <figure key={idx}>
            <a href="#"><img src={item.img} alt={`img${idx + 1}`} /></a>
            <figcaption>
              <div>
                <p className="text01"><a href="#">{item.title}</a></p>
                <p className="text02"><a href="#">{item.desc}</a></p>
              </div>
            </figcaption>
          </figure>
        ))}
      </div>
    </div>
  </section>
);

export default TutorialSection;
