// import React from 'react';

import faq from '../assets/images/icon/faq.svg';
import ticket from '../assets/images/icon/ticket.svg';
import support from '../assets/images/icon/support.svg';
import camera from '../assets/images/icon/camera.svg';
import map from '../assets/images/icon/map.svg';

const supportItems = [
  { icon: faq, label: 'FAQ', alt: 'FAQ' },
  { icon: ticket, label: '정품등록', alt: '정품등록' },
  { icon: support, label: '고객 지원', alt: '고객상담' },
  { icon: camera, label: '렌탈서비스', alt: '렌탈' },
  { icon: map, label: '지점 안내', alt: '지도' },
];

const SupportSection = () => {
  return (
    <section className="section" id="c09">
      <div className="outer">
        <div className="inner">
          <div id="Support">
            {supportItems.map((item, index) => (
              <figure key={index}>
                <a href="#"><img src={item.icon} alt={item.alt} /></a>
                <figcaption><p>{item.label}</p></figcaption>
              </figure>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default SupportSection;
