import React, { useEffect } from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';
// import fullpage from 'fullpage.js';
// import 'fullpage.js/dist/fullpage.css';

import acc01 from '../assets/images/products/camera/acc01.png';
import acc02 from '../assets/images/products/camera/acc02.png';
import acc03 from '../assets/images/products/camera/acc03.png';
import acc04 from '../assets/images/products/camera/acc04.png';
import acc05 from '../assets/images/products/camera/UWP-D27.png';
import acc06 from '../assets/images/products/camera/SF-M512T.png';
import acc07 from '../assets/images/products/camera/VF-82MPAM.png';


const AccessoriesSection = () => {
  // useEffect(() => {
  //   new fullpage('#fullpage', {
  //     anchors: ['accessories'],
  //     scrollOverflow: true,
  //   });

  //   return () => {
  //     fullpage.destroy('all');
  //   };
  // }, []);

  const accessories = [
    { src: acc01, label: 'ILCE-7CM2L' },
    { src: acc02, label: 'ILCE-7CR' },
    { src: acc03, label: 'ILCE-6400K' },
    { src: acc04, label: 'ZV-E10M2' },
    { src: acc05, label: 'ZV-E10M2' },
    { src: acc06, label: 'ZV-E10M2' },
    { src: acc07, label: 'ZV-E10M2' },
  ];

  return (
    <div id="fullpage">
      <section className="section" id="c07">
        <div className="outer">
          <div className="inner">
            <h3>액세서리</h3>
            <div id="Acc">
              <Swiper
                modules={[Navigation, Autoplay]}
                navigation
                autoplay={{ delay: 2500, disableOnInteraction: false }}
                speed={1000}
                breakpoints={{
                  320: { slidesPerView: 3, spaceBetween: 30 },
                  400: { slidesPerView: 4, spaceBetween: 25 },
                  640: { slidesPerView: 3, spaceBetween: 20 },
                  768: { slidesPerView: 3, spaceBetween: 50 },
                  1500: { slidesPerView: 4, spaceBetween: 50 },
                }}
              >
                {accessories.map((item, index) => (
                  <SwiperSlide key={index}>
                    <figure>
                      <img src={item.src} alt={`액세서리${index + 1}`} />
                      <figcaption>
                        <p>{item.label}</p>
                      </figcaption>
                    </figure>
                  </SwiperSlide>
                ))}
              </Swiper>

              <div className="cta03">
                <p><a href="#">더 알아보기</a></p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default AccessoriesSection;
