
//★★★ npm install swiper 필요

import React, { useEffect, useRef } from "react";
import Fullpage from "fullpage.js";
import VideoSection from "../components/VideoSection";
import MainSection1 from "../components/MainSection1";
import MainSection2 from "../components/MainSection2";
import MainSection3 from "../components/MainSection3";
import SectionRecommendAndCommunity from "../components/MainSection3_all";

// import MainSection5 from "../components/MainSection5";
// import Events from "../components/Event";
// import SNSSection from "../components/SNSSection";
// import Footer from "../components/Footer"; 
import "../css/import.css";


const MainPage = () => {
  const fullpageRef = useRef(null);

  useEffect(() => {
    fullpageRef.current = new Fullpage("#fullpage", {
      anchors: ['firstPage', 'secondPage', '3rdPage', '4thPage'],
      navigation: true,
      scrollOverflow: true,
      autoScrolling: true,
      fitToSection: true,
      responsiveWidth: 768,
      //licenseKey: "YOUR_LICENSE_KEY",
      credits: { enabled: false, label: "", position: "right" },
    });

    return () => {
      if (fullpageRef.current) {
        fullpageRef.current.destroy("all");
      }
    };
  }, []);

  return (
    <div id="fullpage">
       <VideoSection />
      <MainSection1 />
      <MainSection2 />
      <MainSection3 />
      <SectionRecommendAndCommunity />
      {/* <Events />
      <SNSSection /> */}
      {/* <Footer />  */}
    </div>
  );
};

export default MainPage;
