// components/MainSection6.jsx (YouTube 사진 예술 슬라이드)

import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Autoplay, EffectCoverflow } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/effect-coverflow';

// import logo from '../assets/images/icon/icon's/Sony_Alpha_logo2.svg';
import photo01 from '../assets/images/community/photo04.png';
import photo02 from '../assets/images/community/photo05.png';
import photo03 from '../assets/images/community/photo06.png';
import photo04 from '../assets/images/community/photo04.png';
import photo05 from '../assets/images/community/photo05.png';
import photo06 from '../assets/images/community/photo06.png';
import photo07 from '../assets/images/community/photo07.png';

const slides = [photo01, photo02, photo03,photo04, photo05, photo06, photo07];

const MainSection6 = () => {
  return (
    <section id="c06">
      <div className="inner">
        {/* <h3><img src={logo} alt="알파로고" /></h3> */}
        <div id="Content-youtube">
          <Swiper
            modules={[Autoplay, EffectCoverflow]}
            slidesPerView={1}
            centeredSlides
            loop
            effect="coverflow"
            autoplay={{ delay: 2500, disableOnInteraction: false }}
            coverflowEffect={{
              rotate: 0,
              stretch: 0,
              depth: 180,
              modifier: 1,
              slideShadows: false,
            }}
            breakpoints={{
              320: { slidesPerView: 2 },
              480: { slidesPerView: 2 },
              768: { slidesPerView: 2 },
            }}
          >
            {slides.map((src, i) => (
              <SwiperSlide key={i}>
                <figure>
                  <img src={src} alt={`Book ${i + 1}`} />
                  <figcaption>
                    <p className="text03">YouTube로 더 많은 사진 예술을 느낄 수 있습니다</p>
                    <p className="text04">소니 카메라와 함께 당신의 창작을 새로운 세계로 초대합니다.</p>
                  </figcaption>
                </figure>
              </SwiperSlide>
            ))}
          </Swiper>
        </div>
      </div>
    </section>
  );
};

export default MainSection6;