import React from "react";
import alpha7 from "../assets/images/products/alpha7.png";
import alpha7Back from "../assets/images/products/alpha7-back.png";

const ProductSection = () => {
  return (
    <div className="section" id="section2">
      <section id="c03">
        <div className="outer">
          <div className="inner">
            <img className="camera03" src={alpha7} alt="카메라 전면" />
            <img className="camera04" src={alpha7Back} alt="카메라 후면" />
            <div className="text">
              <h3>ALPHA 9 III</h3>
              <p className="desc02">The Power of One Frame</p>
              <div className="Cta">
                <p className="cta02"><a href="#">더 알아보기</a></p>
                <p className="cta02-2"><a href="#">구입</a></p>
              </div>
            </div>
          </div> 
        </div>
      </section>
    </div>
  );
};

export default ProductSection;
