import React from "react";
import alpha9 from "../assets/images/products/alpha9.png";
import alpha9Back from "../assets/images/products/alpha9-back.png";

const ProductSection = () => {
  return (
    <div className="section" id="section1">
      <section id="c02">
        <div className="outer">
          <div className="inner">
            <img className="camera01" src={alpha9} alt="카메라 전면" />
            <img className="camera02" src={alpha9Back} alt="카메라 후면" />
            <div className="text">
              <h3>ALPHA 9 III</h3>
              <p className="desc02">The Power of One Frame</p>
              <div className="Cta">
                <p className="cta02"><a href="#">더 알아보기</a></p>
                <p className="cta02-2"><a href="#">구입</a></p>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default ProductSection;
