import React, { useEffect, useState } from "react";

import Logo from "../assets/images/icon/Sony_logo.svg";

const Header = () => {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 100);
    };
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  return (
    <header className={scrolled ? "menu-bg-visible" : ""}>
      <div className="menu-bg"></div>
      <div className="inner">
        <nav id="nav">
          <h1 className="logo">
            <a href="/">
              <img src={ Logo } alt="Sony Logo" />
            </a>
          </h1>
          <ul className="menu">
            <li>
              제품
              <div className="submenu">
                <p>카메라</p>
                <p>비디오 카메라</p>
                <p>렌즈</p>
                <p>액세서리</p>
              </div>
            </li>
            <li>
              이벤트
              <div className="submenu">
                <p>공모전 신청</p>
                <p>이벤트 당첨 확인</p>
                <p>쿠폰 등록</p>
                <p>정품 등록</p>
              </div>
            </li>
            <li>
              고객 서비스
              <div className="submenu">
                <p>공지사항</p>
                <p>FAQ</p>
                <p>구매 상담</p>
                <p>맴버십 등록</p>
              </div>
            </li>
            <li>
              아카데미
              <div className="submenu">
                <p>아카데미 신청</p>
                <p>알파 갤러리</p>
                <p>온라인 강의</p>
                <p>오프라인 강의</p>
              </div>
            </li>
            <li>
              렌탈 서비스
              <div className="submenu">
                <p>카메라</p>
                <p>비디오 카메라</p>
                <p>렌즈</p>
                <p>액세서리</p>
                <p>렌탈 상담</p>
              </div>
            </li>
          </ul>

          <div id="Infomenu">
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
          </div>

          <div id="tm-menu">
            <span><a href="#"></a></span>
            <span><a href="#"></a></span>
          </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;
