// RecommendedProducts.js
import React from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Autoplay } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/navigation';

// 이미지 import
import ILCE7CM2L from '../assets/images/products/camera/ILCE-7CM2L.png';
import ILCE7CR from '../assets/images/products/camera/ILCE-7CR.png';
import ILCE6400K from '../assets/images/products/camera/ILCE-6400K.png';
import ZVE10M2 from '../assets/images/products/camera/ZV-E10M2.png';
import SEL85F14GM2 from '../assets/images/products/camera/SEL85F14GM2.png';
import SEL100400GM from '../assets/images/products/camera/SEL100400GM.png';
import DSCRX100M7 from '../assets/images/products/camera/DSC-RX100M7.png';
import SEL20TC from '../assets/images/products/camera/SEL20TC.png';
import SEL14F18GM from '../assets/images/products/camera/SEL14F18GM.png';
import ILCE1M2 from '../assets/images/products/camera/ILCE-1M2.png';

const products = [
  { img: ILCE7CM2L, name: 'ILCE-7CM2L', price: '2,900,000' },
  { img: ILCE7CR, name: 'ILCE-7CR', price: '3,790,000' },
  { img: ILCE6400K, name: 'ILCE-6400K', price: '1,198,000' },
  { img: ZVE10M2, name: 'ZV-E10M2', price: '1,340,000' },
  { img: SEL85F14GM2, name: 'SEL85F14GM2', price: '2,690,000' },
  { img: SEL100400GM, name: 'SEL100400GM', price: '3,099,000' },
  { img: DSCRX100M7, name: 'DSC-RX100M7', price: '3,099,000' },
  { img: SEL20TC, name: 'SEL20TC', price: '3,099,000' },
  { img: SEL14F18GM, name: 'SEL14F18GM', price: '3,099,000' },
  { img: ILCE1M2, name: 'ILCE-1M2', price: '3,099,000' }
];

const RecommendedProducts = () => {
  return (
    <section id="c04">
      <div className="outer">
        <div className="inner">
          <h3>나에게 맞는 추천 상품</h3>
          <div id="re-product">
            <Swiper
              modules={[Navigation, Autoplay]}
              slidesPerView={1}
              spaceBetween={10}
              navigation={{
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev'
              }}
              autoplay={{
                delay: 2500,
                disableOnInteraction: false
              }}
              breakpoints={{
                300: {
                  slidesPerView: 3,
                  spaceBetween: 20
                },
                660: {
                  slidesPerView: 3,
                  spaceBetween: 20
                },
                700: {
                  slidesPerView: 3,
                  spaceBetween: 20
                }
              }}
              className="swiper-container2"
            >
              {products.map((product, index) => (
                <SwiperSlide key={index}>
                  <figure>
                    <img src={product.img} alt={`추천${index + 1}`} />
                    <figcaption>
                      <p className="P-name">{product.name}</p>
                      <p className="price">{product.price}</p>
                    </figcaption>
                  </figure>
                </SwiperSlide>
              ))}
            </Swiper>

            <div className="cta03">
              <p><a href="#">더 알아보기</a></p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default RecommendedProducts;